<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process274"></a>

<p><br></p>
<p><span class="rvts17">医生的建议（</span><span class="rvts50">Doctor's Order</span><span class="rvts17">）</span><span class="rvts50">.</span><span class="rvts17">下篇</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:408,Y:356</span><span class="rvts21">，地点：邪恶医生的住所</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">发现邪恶医生的秘密，并选择杀掉她或与她签署契约</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1507.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">医生的嘱托支线可以在很多地方触发。在死神海岸的血月岛，通过了解先祖之树的名字，可以知道医生的恶魔身份。在阿克斯矮人的婚礼上，可以通过与婚礼蛋糕互动，找到其中的一张医生留言。</span>
<br><span class="rvts21">种种迹象表明这个医生是一个比较厉害的恶魔。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1508.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">来到医生的住所，我们会首先遇到马拉迪，与她谈话，她会把你传送到一个恶魔的思想空间。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1509.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">在恶魔的空间中，尝试熄灭蜡烛，来削弱医生（或者不熄灭蜡烛，迎来更强大的医生）。如果队伍中有洛许，根据她的剧情需要，是必须熄灭蜡烛的。</span>
<br><span class="rvts21">熄灭蜡烛和不熄灭蜡烛的区别在于以下几个方面：</span>
<br><span class="rvts20">1. </span><span class="rvts21">熄灭蜡烛后的恶魔伤害减半，并且降低</span><span class="rvts20">30%</span><span class="rvts21">的元素抗性；并且无法使用召唤喽啰技能和使用恶魔心灵控制洛许。</span>
<br><span class="rvts20">2. </span><span class="rvts21">未熄灭蜡烛的恶魔伤害可达</span><span class="rvts20">7K</span><span class="rvts21">之高（经典模式），并且元素抵抗较高（至少有两种元素是免疫的），还可以召唤恶魔骷髅以及在第</span><span class="rvts20">3</span><span class="rvts21">轮以后会频繁通过恶魔心灵控制，控制洛许。</span>
<br><span class="rvts21">被恶魔医生控制的洛许，除非在战斗中被杀死，否则将永远无法回归队伍中。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1510.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">另外，在医生住所的海岸处，有一个神奇的带有标示的木桶，如果之前我们在死神海岸的浮木城的渔业港口了解到一批毒鱼被运往了阿克斯，并且后来还在无名岛杀掉了巴萨坦（</span><span class="rvts20">Basatan</span><span class="rvts21">），从他身上获得了</span><span class="rvts20"></span><span class="rvts21">虚空鱼交易卷轴（</span><span class="rvts20">The Voidwaken Fish Scroll</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，那么在这里可以通过使用卷轴，把我们身上的一个传送金字塔传送进入，之后我们会在医生的地下监狱出现。在监狱中，我们可以找到被抓的贾韩以及浮木城旅馆的那个吟游诗人，与他们对话可以获得额外的经验。</span>
<br><span class="rvts21">从地下监狱一路开锁，到达医生的办公室，她会为此而感到惊讶，并想与你签署</span><span class="rvts20"></span><span class="rvts21">一份医生协议</span><span class="rvts20"></span><span class="rvts21">，该协议的内容是在你对峙最终</span><span class="rvts20">BOSS</span><span class="rvts21">布雷克斯以及达利斯等人时，她会出来帮助你。但是条件是你需要杀掉所有队友，之后和她平分神权。</span>
<br><span class="rvts21">如果队伍中有洛许，那么即便从地下监狱来到医生的办公室，她除了吃惊外，并不会给我们这份可以签署的协议。</span>
<br><span class="rvts21">还有一种办法可以不通过木桶传送，也会让医生</span><span class="rvts20"></span><span class="rvts21">认为</span><span class="rvts20"></span><span class="rvts21">你是从她家的地下监狱上来的。你需要较高的潜行和开锁值，逃出医生的视线，打开她身旁那扇通往地下监狱的门，在与贾韩，吟游诗人对话完后，再次上来，她依然会很吃惊的与你对话，并愿意和你签署协议。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1511.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">医生是一个比较难对付的人，她自身攻击和防御都相当高，并且会很多源力法术。而她身边的护士，则会在第一时间跑到她身边，给她提升</span><span class="rvts20">BUFF</span><span class="rvts21">。</span>
<br><span class="rvts21">所以，最安稳的打法就是分散队伍，每个队员看守</span><span class="rvts20">1-2</span><span class="rvts21">个护士，使一名角色与医生对话。第一轮尽可能的杀护士。护士被除掉后，只剩下医生一个人，就比较容易对付。</span>
<br><span class="rvts21">注意，医生自身拥有两种元素的满抗性，并且物理护甲较高，因此需要格外小心。在第一时间除掉护士是打败医生的关键。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1512.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">医生被打败后，洛许的所有个人支线就此完结，会有一段音乐花絮。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



